.checkbox{min-height:1.250rem;min-width:6.250rem}.checkbox__label{white-space:nowrap;display:flex;justify-content:space-between;cursor:pointer}.checkbox__input{display:none}.checkbox__input ~ .checkbox__flag>.checkbox__flag-background{left:100%;top:-180%}.checkbox__input:checked ~ .checkbox__flag>.checkbox__flag-background{left:-50%;top:-50%}.checkbox__input_checked ~ .checkbox__flag>.checkbox__flag-background{animation:background-in .6s forwards}.checkbox__input_unchecked ~ .checkbox__flag>.checkbox__flag-background{animation:background-out .6s forwards}@keyframes background-in{from{left:100%;top:-180%}99%{left:-100%;top:20%}to{left:-50%;top:-50%}}@keyframes background-out{from{left:-50%;top:-50%}70%{left:-100%;top:20%}to{left:100%;top:-180%}}.checkbox__header-text{font-family:Arial, Helvetica, sans-serif;font-size:0.875rem;font-weight:bold;display:flex;align-items:center;background-color:rgba(0,0,0,0.8);color:white;width:100%;padding-left:0.625rem;margin-right:0.313rem}.checkbox__flag{display:flex;justify-content:center;align-items:center;position:relative;width:1.875rem;height:1.875rem;padding-bottom:0.313rem;background-color:white;border:0.125rem black solid;border-radius:0.250rem;box-sizing:border-box;overflow:hidden;flex-shrink:0}.checkbox__flag::after{content:'';position:absolute;width:0.938rem;height:0.438rem;transform:rotate(-45deg);transform-origin:center;border-left:0.250rem white solid;border-bottom:0.250rem white solid}.checkbox__flag-background{position:absolute;width:3.125rem;height:3.125rem;border-radius:50%;background-color:black;transform:rotate(45deg)}
