.j-plugin-slider{position:relative;padding:0.625rem 1.875rem 0.625rem 1.250rem;width:100%}.j-plugin-slider__path{border-radius:0.313rem;width:100%;height:0.313rem;background:#c7c7c7;position:relative}.j-plugin-slider_vertical{padding-top:1.250rem;padding-bottom:1.250rem;width:min-content;height:auto}.j-plugin-slider_vertical .j-plugin-slider__path{height:100%;width:0.313rem}.j-plugin-slider_vertical.j-plugin-slider_with-tip{padding-right:6.250rem;padding-top:1.250rem}.j-plugin-slider_vertical .j-plugin-slider__pointer{transform:translateY(-50%) translateX(-35%)}.j-plugin-slider_vertical .j-plugin-slider__path-line{width:100%}.j-plugin-slider_vertical .j-plugin-slider__tip{top:0;transform:translateX(1.563rem)}.j-plugin-slider_vertical .j-plugin-slider__tip::after{top:30%;left:0px}.j-plugin-slider_with-tip{padding-top:2.500rem}.j-plugin-slider__pointer{width:1.188rem;height:1.188rem;border-radius:50%;position:relative;transform:translateY(-35%) translateX(-50%);background:#f04e32;cursor:pointer;position:absolute;z-index:0;box-sizing:border-box}.j-plugin-slider__pointer_selected{z-index:1}.j-plugin-slider__pointer:hover{z-index:2;background:#da3131}.j-plugin-slider__pointer:hover .j-plugin-slider__tip{background:#da3131}.j-plugin-slider__pointer:hover .j-plugin-slider__tip::after{background:#da3131}.j-plugin-slider__path-line{height:0.313rem;position:absolute;background:#ffd3c9}.j-plugin-slider__tip{min-width:1.875rem;height:1.125rem;position:absolute;padding-top:0.125rem;padding-left:0.500rem;padding-right:0.500rem;top:-1.563rem;left:50%;transform:translateX(-50%);background:#f04e32;color:white;font-size:0.750rem;border-radius:0.250rem;text-align:center;box-sizing:border-box}.j-plugin-slider__tip::after{content:"";position:absolute;width:0.438rem;height:0.438rem;border-radius:0 0 1px 0;background-color:#f04e32;left:50%;transform:translateX(-50%) rotate(45deg);bottom:-0.188rem}
