.sliders{width:100%;height:auto;background:linear-gradient(270deg, #8da3ff, #c2a6fd);background-size:400% 400%;animation:backgroundAnimation 30s ease infinite}@keyframes backgroundAnimation{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}.sliders__element{display:flex;justify-content:center;padding:1.875rem 0.625rem 0 0.625rem;margin:0 auto;max-width:43.750rem}.sliders__element:last-of-type{padding-bottom:1.875rem}
